* {
    font-family: Roboto, sans-serif;
}

.container {
    width: 100%;
    text-align: center;
}

.titleLogin {
    margin: 0 0.9vw 5.6vh;
    font-size: 1.87vw;
    font-weight: 700;
}

.titleBlue {
    color: #101B94;
}

.form-group {
    width: 100%;
    margin-bottom: 3.3vh;
}

.form-change-password {
    width: 100%;
    margin-bottom: 3.3vh;
    text-align: end;
}

.form-control {
    box-sizing: border-box;
    width: 100%;
    height: 7.6vh;
    border: 2px solid #656FE0;
    border-radius: 8px;
    padding: 2.5vh 1.4vw;
    font-size: 1.04vw;
}

.input-invalid {
    border: 2px solid #C80707;
}

input:disabled {
    border: 2px solid #B7B7B7;
}

