.error-message {
    width: 100%;
    font-weight: 700;
    font-size: 0.83vw;
    display: flex;
    align-items: center;
    padding: 0.6vh 0.4vw;
}

.textErrorMessage {
    margin-left: 0.5vw;
    text-align: left;
}

.changePassword {
    text-decoration: none;
    color: #000000;
    font-size: 0.93vw;
    font-weight: 400;
}

.or {
    font-size: 0.93vw;
    margin: 3.3vh 0;
}

.buttonLoginWithGoogle {
    margin-bottom: 3.3vh;
}

.img-google {
    width: 1.7vw;
    margin-right: 0.9vw;
}

.linktoRegister {
    font-size: 1.04vw;
}

.linktoRegister .linktoRegister-text {
    font-weight: 700;
    color: #101B94;
}

@media (max-width: 920px) {
    .form-control {
        height: 5.6vh;
    }

    .input-group-prepend {
        top: 2.8vh;
    }
}